.input {
  background-color: #303245!important;
  border-radius: 12px!important;
  border: 0!important;
  box-sizing: border-box!important;
  color: #eee!important;
  font-size: 18px!important;
  height: 100%!important;
  outline: 0!important;
  padding: 4px 20px 0!important;
  width: 100%!important;
}

.cut {
  background-color: #15172b;
  border-radius: 10px;
  height: 20px;
  left: 20px;
  position: absolute;
  top: -20px;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  transition: -webkit-transform 200ms;
  transition: transform 200ms;
  transition: transform 200ms, -webkit-transform 200ms;
  width: 76px;
}

.cut-short {
  width: 50px;
}

.input:not(:-ms-input-placeholder) ~ .cut {
  transform: translateY(8px);
}

.input:focus ~ .cut,
.input:not(:placeholder-shown) ~ .cut {
  -webkit-transform: translateY(8px);
          transform: translateY(8px);
}

.placeholder {
  color: #65657b;
  font-family: sans-serif;
  left: 20px;
  line-height: 14px;
  pointer-events: none;
  position: absolute;
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
  transition: color 200ms, -webkit-transform 200ms;
  transition: transform 200ms, color 200ms;
  transition: transform 200ms, color 200ms, -webkit-transform 200ms;
  top: 20px;
  opacity : .5;
  background-color: inherit;
}

.input:not(:-ms-input-placeholder) ~ .placeholder {
  transform: translateY(-30px) translateX(10px) scale(0.75)!important;
}

.input:focus ~ .placeholder,
.input:not(:placeholder-shown) ~ .placeholder {
  -webkit-transform: translateY(-30px) translateX(10px) scale(0.75)!important;
          transform: translateY(-30px) translateX(10px) scale(0.75)!important;
}

.input:not(:-ms-input-placeholder) ~ .placeholder {
  color: #808097;
}

.input:not(:placeholder-shown) ~ .placeholder {
  color: #808097;
}

.input:focus ~ .placeholder {
  color: #dc2f55;
}

